import { Injectable } from '@angular/core';
import { HttpService } from "../../providers/http.service";
import { NativeService } from "../../providers/NativeService";
import 'rxjs/add/operator/toPromise';
@Injectable()
export class MapService {
  constructor(private httpService: HttpService, public native: NativeService) {

  }
  uploadCurLoc(loc) {//
    let reqinfo = {
      url: 'person/addlocation',
      personid: this.native.UserSession&&this.native.UserSession._id,
      curlocation: {
        positioningdate: new Date(),
        SRS: '4321',
        geolocation: loc
      },
      hideloading: true
    }
    this.httpService.post(reqinfo.url, reqinfo).subscribe(
      data => {
        try {
          let res= data.json();
          console.log('');
        } catch (error) {
          console.log('');
        }
      },
      err => { console.log(''); }
    );
  }
  getDeptPerson() {//
    let reqinfo = {
      url: 'maproute/getworkmateinfo',
      _id: this.native.UserSession&&this.native.UserSession._id,
      hideloading: true
    }
    return new Promise((resolve, reject) => {
      this.httpService.post(reqinfo.url, reqinfo).subscribe(
        data => {
          try {
            let res = data.json();
            resolve(res.success);
          } catch (error) {
            reject(error);
          }
        },
        err => { reject(err); }
      );
    });

  }
  getcameraposition() {//
    let reqinfo = {
      url: 'maproute/getcameraposition',
      hideloading: true
    }
    return new Promise((resolve, reject) => {
      this.httpService.post(reqinfo.url, reqinfo).subscribe(
        data => {
          try {
            let res = data.json();
            resolve(res.success);
          } catch (error) {
            reject(error);
          }
        },
        err => { reject(err); }
      );
    });
  }
   geteventposition() {//
    let reqinfo = {
      url: 'maproute/geteventposition',
      hideloading: true
    }
    return new Promise((resolve, reject) => {
      this.httpService.post(reqinfo.url, reqinfo).subscribe(
        data => {
          try {
            let res = data.json();
            resolve(res.success);
          } catch (error) {
            reject(error);
          }
        },
        err => { reject(err); }
      );
    });
  }
  getspotarea() {//
    let reqinfo = {
      url: 'maproute/getspotarea',
      hideloading: true
    }
    return new Promise((resolve, reject) => {
      this.httpService.post(reqinfo.url, reqinfo).subscribe(
        data => {
          try {
            let res = data.json();
            resolve(res.success);
          } catch (error) {
            reject(error);
          }
        },
        err => { reject(err); }
      );
    });
  }
}


