/**
 * 
 */
import { Injectable } from '@angular/core';
import { ToastController, LoadingController, Platform, Loading, AlertController } from 'ionic-angular';
import { Camera } from '@ionic-native/camera';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { Transfer, TransferObject } from '@ionic-native/transfer';
import { Storage } from '@ionic/storage';
declare var cordova: any;
@Injectable()
export class NativeService {
    private fileTransfer: TransferObject;
    private loading: Loading;
    private loadingIsOpen: boolean = false;
    //
    public appServer = {
        //nodejs
        node: "http://gridmap.xiaoketech.com/",
        //
        file: "http://gridmap-file.xiaoketech.com/"
    }
    myStorage: Storage;
    // null null  
    public UserSession: any;
    constructor(private platform: Platform,
        private toastCtrl: ToastController,
        private alertCtrl: AlertController,
        private camera: Camera,
        private file: File,
        private fileOpener: FileOpener,
        private loadingCtrl: LoadingController,
        private transfer: Transfer,
        public storage: Storage, ) {
        this.myStorage = storage;
        this.fileTransfer = this.transfer.create();
    }
    /**
     * 
     * @param message 
     * @param duration 
     */
    showToast(message: string = '', duration: number = 2000): void {
        this.toastCtrl.create({
            message: message,
            duration: duration,
            position: 'bottom',
            cssClass: 'custoast',
            showCloseButton: false
        }).present();
    };
    /**
      * 
      * 
      * 
      */
    AjAxData(data, succallback?: Function, failcallback?: Function): void {
        try {
            let res = data.json();
            if (res) {
                succallback && succallback(res);
            } else {
                failcallback && failcallback(res);
            }
        } catch (error) {
            console.error(error);
            this.showToast(error);
        }
    }
    //APK copy
    downapk(apk_down, apk_name) {
        if (this.isAndroid()) {
            let alertx = this.alertCtrl.create({
                title: '0%',
                enableBackdropDismiss: false,
                buttons: ['']
            });
            alertx.present();
            const apk = this.file.externalApplicationStorageDirectory + apk_name + '.apk'; //apk
            this.fileTransfer.download(apk_down, apk).then(() => {
                alertx.dismiss();
                this.fileOpener.open(apk, 'application/vnd.android.package-archive').then(() => { }, (error) => {
                    alert(JSON.stringify(error));
                });
            }, (error) => {
                alert(JSON.stringify(error));
                alertx.dismiss();
            });
            this.fileTransfer.onProgress((event: ProgressEvent) => {
                let num = Math.floor(event.loaded / event.total * 100);
                if (num === 100) {
                    alertx.dismiss();
                } else {
                    alertx.setTitle('' + num + '%');
                }
            });
        }
    }
    /**
   * 
   * @return {boolean}
   */
    isMobile(): boolean {
        return this.platform.is('mobile') && !this.platform.is('mobileweb');
    }

    /**
     * android
     * @return {boolean}
     */
    isAndroid(): boolean {
        return this.isMobile() && this.platform.is('android');
    }

    /**
     * loading
     * @param content 
     */
    showLoading(content: string = ''): void {
        if (!this.loadingIsOpen) {
            this.loadingIsOpen = true;
            this.loading = this.loadingCtrl.create({
                content: content
            });
            this.loading.present();
            //setTimeout(() => {//10
            //    this.loadingIsOpen && this.loading.dismiss();
            //    this.loadingIsOpen = false;
            //}, 10000);
        }
    };

    /**
     * loading
     */
    hideLoading(): void {
        this.loadingIsOpen && this.loading.dismiss();
        this.loadingIsOpen = false;
    };
    //64
    tobase64(url: string, path: string) {
        return this.file.readAsDataURL(path ? path : cordova.file.externalRootDirectory, url)
    }
    /**
    * cordova-plugin-camerabase64
    * @param options
    * @return {Promise<T>}
    */
    getPicture = (options) => {
        return new Promise((resolve, reject) => {
            this.camera.getPicture(Object.assign({
                sourceType: this.camera.PictureSourceType.CAMERA,//,CAMERA:,PHOTOLIBRARY:
                destinationType: this.camera.DestinationType.DATA_URL,//,DATA_URL:base64,FILE_URI:
                quality: 90,//0 - 100
                allowEdit: true,//
                encodingType: this.camera.EncodingType.JPEG,
                targetWidth: 800,//
                targetHeight: 800,//
                saveToPhotoAlbum: false,//
                correctOrientation: true//
            }, options)).then((imageData) => {
                resolve(imageData);
            }, (err) => {
                console.log(err);
                err == 20 ? this.showToast(',') : reject(err);
            });
        });
    };
    /**
    * 
    * @param options
    * @return {Promise<T>}
    */
    getPictureByPhotoLibrary = (options = {}) => {
        return new Promise((resolve) => {
            this.getPicture(Object.assign({
                sourceType: this.camera.PictureSourceType.PHOTOLIBRARY
            }, options)).then(imageBase64 => {
                resolve(imageBase64);
            }).catch(err => {
                String(err).indexOf('cancel') != -1 ? this.showToast('', 1500) : this.showToast('');
            });
        });
    };
    /**
     * 
     * @param options
     * @return {Promise<T>}
     */
    getPictureByCamera = (options = {}) => {
        return new Promise((resolve) => {
            this.getPicture(Object.assign({
                sourceType: this.camera.PictureSourceType.CAMERA
            }, options)).then(imageBase64 => {
                resolve(imageBase64);
            }).catch(err => {
                String(err).indexOf('cancel') != -1 ? this.showToast('', 1500) : this.showToast('');
            });
        });
    };
    /**
    * 
    * @param 
    */
    alert(msg: string, sucCallback?: Function, flg: boolean=true, btntxt: string = '', title: string = '', ): void {
        let alert = this.alertCtrl.create({
            title: title,
            message: msg,
            enableBackdropDismiss:flg,
            buttons: [
                {
                    text: btntxt,
                    handler: () => {
                        sucCallback && sucCallback();
                    }
                }]
        });
        alert.present();
    }
    /**
    * 
    * @param 
    */
    confirm(msg: string, sureCallback: Function, cancelCallback?: Function, title: string = '',cancel:string='',ok:string=''): void {
        let confirm = this.alertCtrl.create({
            title: title,
            message: msg,
            buttons: [
                {
                    text: cancel,
                    role: 'cancel',
                    cssClass: 'cus-cancel',
                    handler: () => {
                        cancelCallback && cancelCallback();
                    }
                },
                {
                    text: ok,
                    handler: () => {
                        sureCallback && sureCallback();
                    }
                }
            ]
        });
        confirm.present();
    }
    /**  
   * URL  
   * URL:http://www.baidu.com/index?name=tyler  
   * paramName URL  
   * :getParam("name","url")  
   * :tyler  
   */
    getParam(paramName, url) {
        let strReturn = "";
        let strHref = url.toLowerCase();
        if (strHref.indexOf("?") > -1) {
            let strQueryString = strHref.substr(strHref.indexOf("?") + 1).toLowerCase();
            let aQueryString = strQueryString.split("&");
            for (let iParam = 0; iParam < aQueryString.length; iParam++) {
                if (aQueryString[iParam].indexOf(paramName.toLowerCase() + "=") > -1) {
                    let aParam = aQueryString[iParam].split("=");
                    strReturn = aParam[1];
                    break;
                }
            }
        } else if (strHref.indexOf("&") > -1) {
            let strQueryString = strHref.substr(strHref.indexOf("&") + 1).toLowerCase();
            let aQueryString = strQueryString.split("&");
            for (let iParam = 0; iParam < aQueryString.length; iParam++) {
                if (aQueryString[iParam].indexOf(paramName.toLowerCase() + "=") > -1) {
                    let aParam = aQueryString[iParam].split("=");
                    strReturn = aParam[1];
                    break;
                }
            }
        }
        return strReturn;
    }
}



