import { Injectable } from '@angular/core';
import { HttpService } from "../../providers/http.service";
import { NativeService } from "../../providers/NativeService";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Observable } from "rxjs";
/*
  Generated class for the LoginServiceProvider provider.
  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
declare var FileTransfer: any;
declare var FileUploadOptions: any;
@Injectable()
export class LoginService {

  constructor(private httpService: HttpService, public native: NativeService, private camera: Camera) {

  }
  getUserByUUid(uuid): Observable<Response> {//uuid
    let requestInfo = {
      url: "people/uuid",
      uuid: uuid,
      key: '123123',
      hideloading: true
    }
    return Observable.create((observer) => {
      this.httpService.post(requestInfo.url, requestInfo).subscribe(
        data => {
          try {
            //
            let res = data.json();
            if (res.code != 200) {
              observer.error(res.info);
            } else {
              observer.next(res.info);
            }

          } catch (error) {
            observer.error(error);
          }
        },
        err => observer.error(err)
      );
    })
  }

  processIDcard = function (FileData, callbank) {  //
    this.native.showLoading('...');
    let requestInfo = {
      url: "/people/identification",
      FileData: FileData,
      hideloading: true
    }
    this.httpService.post(requestInfo.url, requestInfo).subscribe(
      data => {
        try {
          let res = data.json();
          if (res.code != 200) {
            this.native.showToast(res.info);
          } else {
            callbank && callbank(res.info);
          }
        } catch (error) {
          this.native.showToast('~');
        }
        this.native.hideLoading();
      },
      err => {
        this.native.showToast('~');
        this.native.hideLoading();
      }
    );
  }
  registered(person) {//
    let requestInfo = Object.assign({
      'url': "/people/register",
    }, person);
    return Observable.create((observer) => {
      this.httpService.post(requestInfo.url, requestInfo).subscribe(
        data => {
          try {
            let res = data.json();
            if (res.code != 200) {
              observer.error(res.error);
            } else {
              observer.next(res);
            }

          } catch (error) {
            observer.error(error);
          }
        },
        err => {
          observer.error(err);
        }
      );
    })
  }
  openCamera(callbank) {//
    let options: CameraOptions = {
      //base64
      destinationType: this.camera.DestinationType.DATA_URL,
      mediaType: this.camera.MediaType.PICTURE,
      quality: 100,
      targetWidth: 700,
      targetHeight: 440
    }
    this.native.getPicture(options).then((imageData) => {
      this.processIDcard(imageData, callbank);
      //this.uploadIDCard(imageData, callbank);
    }, (err) => {
      this.native.showToast('');
    });
  }
  //,base64
  uploadIDCard(fileURL, callbank) {
    let options = new FileUploadOptions();
    options.fileKey = "file";
    options.fileName = fileURL.substr(fileURL.lastIndexOf('/') + 1);
    options.mimeType = "image/jpg";
    //jpg
    let ft = new FileTransfer();
    this.native.showLoading('...');
    ft.upload(fileURL, this.native.appServer.node + 'filedirectupload/IDCard', data => {
      console.log(data);
      this.native.hideLoading();
      try {
        let res = JSON.parse(data.response);
        this.processIDcard(res.filename, callbank);
      } catch (error) {
        this.native.showToast('IDCard');
      }
    }, err => {
      console.log(err);
      this.native.showToast('');
      this.native.hideLoading();
    }, options);
  }
}


