import { NgModule, ErrorHandler,} from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HttpModule } from "@angular/http";
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativeService } from "../providers/NativeService";
import { HttpService } from "../providers/http.service";
import { ChatService } from "../providers/chat-service";
import { IonicStorageModule } from '@ionic/storage';
import { LoginService } from '../pages/login/login-service';
import { MapService } from '../pages/home/map-service';
import { MentService } from "../pages/ment/ment.service";
import { Camera } from '@ionic-native/camera';
import { UniqueDeviceID } from '@ionic-native/unique-device-id';
import { MediaPlugin } from '@ionic-native/media';
import { MediaCapture } from '@ionic-native/media-capture';
import { Geolocation } from '@ionic-native/geolocation';
import { File } from '@ionic-native/file';
import { Device } from '@ionic-native/device';
import { Badge } from '@ionic-native/badge';
import { IonJPushModule } from 'ionic2-jpush';
import {TabsPageModule} from "../pages/tabs/tabs.module";
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { Transfer } from '@ionic-native/transfer';
import { FileOpener } from '@ionic-native/file-opener';
import { Sim } from '@ionic-native/sim';
import { AppMinimize } from '@ionic-native/app-minimize';
@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    IonJPushModule,
    BrowserModule,
    HttpModule,
    TabsPageModule,
    IonicModule.forRoot(MyApp, {
      tabsHideOnSubPages: "true", // navpush
      backButtonText: '',
      iconMode: 'ios',
      mode: 'ios'
    }, 
{
  links: [
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/chat/chat.module#ChatPageModule', name: 'ChatPage', segment: 'chat', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/chat/select/select.module#SelectPageModule', name: 'SelectPage', segment: 'select', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/chat_user/chat_user.module#ChatUserPageModule', name: 'ChatUserPage', segment: 'chat_user', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/location/location.module#LocationPageModule', name: 'LocationPage', segment: 'location', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/peslist/peslist.module#PeslistPageModule', name: 'PeslistPage', segment: 'peslist', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/registinfo/registinfo.module#RegistinfoPageModule', name: 'RegistinfoPage', segment: 'registinfo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/registtip/registtip.module#RegisttipPageModule', name: 'RegisttipPage', segment: 'registtip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/ment.module#MentPageModule', name: 'MentPage', segment: 'ment', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/case/case.module#casePageModule', name: 'casePage', segment: 'case', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/casereport.module#CaseReportPageModule', name: 'CaseReportPage', segment: 'casereport', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/falv/falv.module#falvPageModule', name: 'falvPage', segment: 'falv', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/reject/reject.module#rejectPageModule', name: 'rejectPage', segment: 'reject', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/select/select.module#SelectUserPageModule', name: 'SelectUserPage', segment: 'select', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/step/step.module#stepPageModule', name: 'stepPage', segment: 'step', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/casereport/step/image/image.module#imagePageModule', name: 'imagePage', segment: 'image', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/statistics/statistics.module#StatisticsPageModule', name: 'StatisticsPage', segment: 'statistics', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/statistics/charts/charts.module#ChartsPageModule', name: 'ChartsPage', segment: 'charts', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/stroke/stroke.module#StrokePageModule', name: 'StrokePage', segment: 'stroke', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/upcoming/upcoming.module#UpcomingPageModule', name: 'UpcomingPage', segment: 'upcoming', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ment/verify/verify.module#verifyPageModule', name: 'verifyPage', segment: 'verify', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tongzhi/tongzhi.module#TongzhiPageModule', name: 'TongzhiPage', segment: 'tongzhi', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/user.module#UserPageModule', name: 'UserPage', segment: 'user', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/leave/leave.module#LeavePageModule', name: 'LeavePage', segment: 'leave', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/password/password.module#PasswordPageModule', name: 'PasswordPage', segment: 'password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/reported/reported.module#ReportedPageModule', name: 'ReportedPage', segment: 'reported', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/scanlogin/scanlogin.module#ScanloginPageModule', name: 'ScanloginPage', segment: 'scanlogin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/shift/shift.module#ShiftPageModule', name: 'ShiftPage', segment: 'shift', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user/shift/newper/newper.module#NewperPageModule', name: 'NewperPage', segment: 'newper', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    NativeService,
    HttpService,
    ChatService,
    MentService,
    Camera,
    UniqueDeviceID,
    MediaPlugin,
    MediaCapture,
    Geolocation,
    File,
    MapService,
    Device,
    Sim,
    Badge,
    Transfer,
    BarcodeScanner,
    FileOpener,
    AppMinimize,
    { provide: ErrorHandler, useClass: IonicErrorHandler }, LoginService,
  ]
})
export class AppModule { }



